:root {
  --orange: hsl(26, 100%, 55%);
  --pale-orange: hsl(25, 100%, 94%);
  --dark-blue: hsl(220, 13%, 13%);
  --dark-grayish-blue: hsl(219, 9%, 45%);
  --light-grayish-blue: hsl(223, 64%, 98%);
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --lightbox-background: hsl(0, 0%, 75%);
  --header-line: hsla(0, 0%, 75%, 0.622);
}

:root {
  --font-kumbh: "Kumbh Sans", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-kumbh);
  background-color: var(--white);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}

p {
  font-size: 1rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
}

.container {
  max-width: 90rem;
  margin: 0 auto;
}

.header .container {
  background-color: var(--white);
  padding: 1.25rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 40em) {
  .header .container {
    padding: 1.25rem 1.25rem 1.875rem 1.25rem;
  }
}
.header__left {
  display: flex;
  align-items: center;
  gap: 1.125rem;
}
@media (min-width: 40em) {
  .header__left {
    flex-direction: row-reverse;
    gap: 2.1875rem;
  }
}
.header__right {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.25rem;
}
@media (min-width: 40em) {
  .header__right {
    gap: 2.0625rem;
  }
}
.header__right--avatar {
  max-width: 1.5rem;
  margin: 6px;
  vertical-align: middle;
  border-radius: 50%;
  transition: box-shadow 0.3s;
  box-shadow: 0 0 0 1.5px #ddd;
}
.header__right--avatar:hover {
  box-shadow: 0 0 0 1.5px var(--orange);
  cursor: pointer;
}
@media (min-width: 40em) {
  .header__right--avatar {
    max-width: 2.1875rem;
    margin: 0;
  }
}
.header__right--cart {
  fill: #69707D;
  max-width: 1.25rem;
}
@media (min-width: 40em) {
  .header__right--cart {
    max-width: 1.125rem;
  }
}
.header__right--cart:hover {
  filter: invert(1) brightness(0) saturate(100%) hue-rotate(0deg);
  cursor: pointer;
}

.header__line {
  display: none;
  margin: 0;
  border: 0.11375rem solid var(--header-line);
  border-bottom: none;
  border-right: none;
  border-left: none;
}
@media (min-width: 40em) {
  .header__line {
    display: block;
  }
}

.hamburger-menu .menu-icon {
  max-width: 100%;
}
.hamburger-menu__open, .hamburger-menu__close {
  display: block;
  border: 0;
  background: none;
  cursor: pointer;
  outline: none;
}
@media (min-width: 40em) {
  .hamburger-menu__open, .hamburger-menu__close {
    display: none;
  }
}
.hamburger-menu__close {
  position: relative;
  width: 20px;
  height: 20px;
}
.hamburger-menu__close--img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.navbar {
  opacity: 0;
  visibility: hidden;
}
@media (min-width: 40em) {
  .navbar {
    display: block;
    opacity: 1;
    visibility: visible;
  }
}
@media (max-width: 39.9475em) {
  .navbar {
    display: flex;
    flex-direction: column;
    gap: 2.375rem;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    background-color: var(--white);
    padding: 1.375rem 1.375rem;
    width: 67%;
    box-shadow: 0 0 0 100vmax rgba(0, 0, 0, 0.5);
  }
}
.navbar.visible {
  opacity: 1;
  visibility: visible;
}
.navbar__list {
  display: flex;
  flex-direction: column;
  list-style-type: none;
}
@media (min-width: 40em) {
  .navbar__list {
    flex-direction: row;
    gap: 1rem;
  }
}
.navbar__list li {
  padding: 0.71875rem 0;
}
@media (min-width: 40em) {
  .navbar__list li {
    padding: 0;
  }
}
.navbar__list a {
  text-decoration: none;
  color: var(--black);
  font-weight: 700;
}
@media (min-width: 40em) {
  .navbar__list a {
    color: var(--dark-grayish-blue);
    font-weight: 400;
    position: relative;
  }
  .navbar__list a:hover::after {
    content: "";
    display: block;
    width: 100%;
    height: 4px;
    background-color: var(--orange);
    position: absolute;
    bottom: -38px;
    left: 50%;
    transform: translate(-50%, 0);
  }
}
.navbar__list a:hover {
  color: var(--black);
}